import { TextInput,  ActionIcon, useMantineTheme, UnstyledButton } from '@mantine/core';
import { IconClipboardCheck, IconArrowRight, IconArrowLeft } from '@tabler/icons';

export function ReferralInput(props) {
  const theme = useMantineTheme();
 

  return (
    <TextInput disabled xs={{ flex: 1 }}
    
      radius="sm"
      size="md"
      value={props.referralLink}
      rightSection={
        <ActionIcon onClick={() => navigator.clipboard.writeText(props.referralLink)} size={32} radius="xl" color={theme.primaryColor} variant="filled">
         <IconClipboardCheck size={18} stroke={1.5} />
      
        </ActionIcon>
      }
      placeholder="Search questions"
      rightSectionWidth={42}
      {...props}
    />
  );
}