import React from 'react';
import { createStyles, ThemeIcon, Progress, Text, Group, Badge, Paper } from '@mantine/core';
import moment from 'moment'
const ICON_SIZE = 60;

const useStyles = createStyles((theme) => ({
  card: {
    position: 'relative',
    overflow: 'visible',
    padding: theme.spacing.xl,
    paddingTop: theme.spacing.xl * 1.5 + ICON_SIZE / 3,
  },

  icon: {
    position: 'absolute',
    top: -ICON_SIZE / 3,
    left: `calc(50% - ${ICON_SIZE / 2}px)`,
  },

  title: {
    fontFamily: `Greycliff CF, ${theme.fontFamily}`,
    lineHeight: 1,
  },
}));

export function InvestmentCard(props) {
  const { classes } = useStyles();

  return (
    <Paper radius="md" withBorder className={classes.card} m={10} mt={ICON_SIZE / 3}>
      <ThemeIcon className={classes.icon} size={ICON_SIZE} radius={ICON_SIZE}>
        Plan {props.plan / 1 + 1}
      </ThemeIcon>

      <Text align="center" weight={700} className={classes.title}>
      {moment(props.start * 1000).format('DD MMM')}  {moment(props.finish * 1000).format('DD MMM')}
      </Text>
      <Text color="dimmed" align="center" size="sm">
      {(props.percent / 10).toFixed(1)}% 
      </Text>

      <Group position="apart" mt="xs">
        <Text size="sm" color="teal">
        {(props.amount / 1e18).toFixed(3)}
        </Text>
        <Text size="sm" color="yellow">
        {(((new Date() / 1e3 - props.start) / (props.finish - props.start)) * 100).toFixed(2)+'%'}
        </Text>
        <Text size="sm" color="red">
        {(props.profit / 1e18).toFixed(3)}
        </Text>
      </Group>
      <Progress mt={5}
           // classNames={{ bar: classes.progressBar }}
            value={(((new Date() / 1e3 - props.start) / (props.finish - props.start)) * 100).toFixed(2)} 
          />
      
      <Group position="apart" mt="md">
        <Text size="sm"></Text>
        <Badge size="sm">{moment(moment(props.finish * 1000).format('YYYY-MM-DD')).diff(moment(), 'days')} days left</Badge>
      </Group>
    </Paper>
  );
}