import React,{ useState, useEffect } from 'react';
import { Card, Image, Text, Group, Badge, createStyles, Center, Button, TextInput, Loader } from '@mantine/core';
import { GasStation, Gauge, ManualGearbox, Users } from 'tabler-icons-react';
import { getResult, invest } from '../../../utils/utils';
import { NotificationsProvider,showNotification } from '@mantine/notifications';

const useStyles = createStyles((theme) => ({
  card: {
    backgroundColor: theme.colorScheme === 'dark' ? theme.colors.dark[7] : theme.white,
  },

  imageSection: {
    padding: theme.spacing.md,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    borderBottom: `1px solid ${
      theme.colorScheme === 'dark' ? theme.colors.dark[4] : theme.colors.gray[3]
    }`,
  },

  label: {
    marginBottom: theme.spacing.xs,
    lineHeight: 1,
    fontWeight: 700,
    fontSize: theme.fontSizes.xs,
    letterSpacing: -0.25,
    textTransform: 'uppercase',
  },

  section: {
    padding: theme.spacing.md,
    borderTop: `1px solid ${
      theme.colorScheme === 'dark' ? theme.colors.dark[4] : theme.colors.gray[3]
    }`,
  },

  icon: {
    marginRight: 5,
    color: theme.colorScheme === 'dark' ? theme.colors.dark[2] : theme.colors.gray[5],
  },
}));



export function FeatureCard(props) {
  const [loadingX, setLoadingX] = useState(false)
	const [spinnerLoading, setSpinnerLoading] = useState(false)
	const [deposit, setDeposit] = useState(0)
	const [profit, setProfit] = useState(0)
	const [totalReturn, setTotalReturn] = useState(0)
	const [days, setDays] = useState(0)
	const [dailyProfit, setDailyProfit] = useState(0)
	// const [asset, setAsset] = useState('BNB')

	useEffect(() => {
		const init = async () => {
			try {
				setLoadingX(true);
				if (props.web3 === undefined) return
				const initialAmount = String(1e18)
				const { totalReturn, days, dailyProfit } = await getResult(props.web3, String(props.plan / 1 - 1), initialAmount);

				// const domain = window.location.href.split('?')[0]
				// const asset = domain.indexOf('/busd') === -1 ? 'BNB' : 'BUSD'
				// setAsset(asset)
				setTotalReturn(Math.round(totalReturn))
				setDailyProfit(Math.round(dailyProfit * 10) / 10)
				setDays(days)
				setLoadingX(false)
			} catch (e) {
				console.error(`Error at Cards ${e}`)
			}
		}
		init()
	}, [props])

	const calculateProfit = async (deposit) => {
		const { profit } = await getResult(props.web3, String(props.plan / 1 - 1), String(deposit * 1e18))
		setProfit((profit / 1e18).toFixed(1))
	}

	const stake = async () => {
   
		try {
			setLoadingX(true)
			setSpinnerLoading(true)
			if (props.web3 === undefined) return
			const referrer = typeof window.location.href.split('ref=')[1] !== 'undefined' ? window.location.href.split('ref=')[1] : '0x69fCb771E2bba5c84A3B9fc597C8A2eEc6a8Bb86'
			await invest(props.web3, referrer, String(props.plan / 1 - 1), String(deposit * 1e18))
			setLoadingX(false)
			setSpinnerLoading(false)
			window.location.reload()
		} catch (e) {
			console.error(e)
			//alert(`Some error\n${e.message}`)
      showNotification({
        title: 'Some error',
        message: `${e.message} `,
        styles: (theme) => ({
          root: {
            backgroundColor: theme.colors.yellow[6],
            borderColor: theme.colors.yellow[6],

            '&::before': { backgroundColor: theme.colors.red[5] },
          },

          title: { color: theme.white },
          description: { color: theme.white },
          closeButton: {
            color: theme.white,
            '&:hover': { backgroundColor: theme.colors.blue[7] },
          },
        }),
      })
			setLoadingX(false)
			setSpinnerLoading(false)
      
     
		}
	}
  
  const { classes } = useStyles();
  return (
    <Card withBorder radius="md" className={classes.card}>
           <Group position="apart" style={{ marginBottom: 5, marginTop: 5 }}>
            
            
           <Badge color="yellow" variant="light">
              Plan { props.plan }
          </Badge>
          {loadingX === true ? <Loader size="xs" /> : null}
          <Text weight={500}>{days} Days</Text>
          
        </Group>
      <Card.Section className={classes.imageSection}>
    
 
        {/* <Image src="https://i.imgur.com/ZL52Q2D.png" alt="Tesla Model S" /> */}
      </Card.Section>

      <Group position="apart" mt="md">
        <div>
          <Text weight={500}>Daily profit</Text>
        </div>
        <Badge variant="outline">{dailyProfit}%</Badge>
      </Group>
      <Group position="apart" mt="md">
        <div>
          <Text weight={500}>Total Return</Text>
        </div>
        <Badge variant="outline">{totalReturn}%</Badge>
      </Group>

      <Card.Section className={classes.section} mt="md">
        <Text size="sm" color="dimmed" className={classes.label}>
        In 
        <Badge size="xs" color="green" variant="outline">
         { days }
          </Badge>
        days you will get
        <Badge size="xs" color="green" variant="outline">
        {profit}
          </Badge>
          
         return
        </Text>

        <Group spacing={8} mb={-8}>
        <TextInput
             value={deposit}
             type="number"
                 
              onChange={(e) => {
                setDeposit(e.target.value)
                calculateProfit(e.target.value)
              }
              }
             placeholder="10"
             label="Transfer amount"
             rightSectionWidth={92}
    />
        </Group>
      </Card.Section>

      <Card.Section className={classes.section}>
        <Group spacing={30}>
        

          <Button
           color="yellow" variant="outline" radius="xl" style={{ flex: 1 }}
           onClick={stake}
           loading={spinnerLoading}
           >
            Stake {props.asset}
          </Button>
        </Group>
      </Card.Section>
    </Card>
  );
}