import React from 'react';
import { Card, Image, Text, Group, Badge, createStyles, Center, Button } from '@mantine/core';
import { GasStation, Gauge, ManualGearbox, Users } from 'tabler-icons-react';
import { ReferralInput } from '../inputs/Referralnput';

const useStyles = createStyles((theme) => ({
  card: {
    backgroundColor: theme.colorScheme === 'dark' ? theme.colors.dark[7] : theme.white,
  },

  imageSection: {
    padding: theme.spacing.md,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    borderBottom: `1px solid ${
      theme.colorScheme === 'dark' ? theme.colors.dark[4] : theme.colors.gray[3]
    }`,
  },

  label: {
    marginBottom: theme.spacing.xs,
    lineHeight: 1,
    fontWeight: 700,
    fontSize: theme.fontSizes.xs,
    letterSpacing: -0.25,
    textTransform: 'uppercase',
  },

  section: {
    padding: theme.spacing.md,
    borderTop: `1px solid ${
      theme.colorScheme === 'dark' ? theme.colors.dark[4] : theme.colors.gray[3]
    }`,
  },

  icon: {
    marginRight: 5,
    color: theme.colorScheme === 'dark' ? theme.colors.dark[2] : theme.colors.gray[5],
  },
}));

const mockdata = [
  { label: '4 passengers', icon: Users },
  { label: '100 km/h in 4 seconds', icon: Gauge },
  { label: 'Automatic gearbox', icon: ManualGearbox },
  { label: 'Electric', icon: GasStation },
];

const WalletReferralCard = (props) => {
  const { classes } = useStyles();
  const features = mockdata.map((feature) => (
    <Center key={feature.label}>
      <feature.icon size={18} className={classes.icon} />
      <Text size="xs">{feature.label}</Text>
    </Center>
  ));

  return (
    <Card withBorder radius="md" className={classes.card}>
      <Card.Section ml="xs" mt="xs">
      <Text weight={500}>Referal Link</Text>
     </Card.Section>

      <Card.Section className={classes.section} mt="md">
         <ReferralInput referralLink={props.referralLink} />
      </Card.Section>
      

      <Card.Section className={classes.section}>
      <Group position="apart" mt="md">
        <div>
          <Text weight={500}>Total Referral Earned</Text>
        </div>
        <Badge size="xl" color="green"variant="outline">{props.userReferralTotalBonus}</Badge>
      </Group>
      </Card.Section>
      <Card.Section className={classes.section} mt="md">
      
      <Group position="apart" mt="md">
        <div>
          <Text weight={500}>Total Referral Withdrawn</Text>
        </div>
        <Badge size="xl" color="green"variant="outline">{props.userReferralWithdrawn}</Badge>
      </Group>
      </Card.Section>
      <Card.Section className={classes.section} mt="md">
      
      <Group position="apart" mt="md">
        <div>
          <Text weight={500}>Invited Users by You</Text>
        </div>
        <Badge size="xl" color="green"variant="outline">{props.invitedTotal}</Badge>
      </Group>
      
      </Card.Section>
    </Card>
  );
}
export default WalletReferralCard;


