import { useState, useEffect , Suspense }  from 'react'
import Web3 from 'web3'
import {
  AppShell,
  MantineProvider,
  Grid,
  Center,
  SimpleGrid,
  Box,
  Container,
  Skeleton,
  Loader,
  useMantineTheme,
} from '@mantine/core';
import { HeaderNavBar, SideNavBar } from '../../components';
import { FeatureCard } from '../../components/dashboard/card/FeatureCard';
import { getAssetPrice } from '../../utils/utils'
import { NotificationsProvider } from '@mantine/notifications';
import { StakedStatisticCard } from '../../components/dashboard/StatsStakedCard';
import Stake from '../../components/dashboard/Stake';
import { Outlet } from 'react-router-dom';

const { REACT_APP_NETWORK_ID } = process.env;

 const DashboardLayout = () =>{
  const theme = useMantineTheme();
    const [web3, setWeb3] = useState(undefined)
    const [account, setAccount] = useState('')
    const [assetPrice, setAssetPrice] = useState(0)
    const [asset, setAsset] = useState('')
    // const [metamaskChange, setMetaMaskChange] = useState(true)
    const [wrongNetwork, setWrongNetwork] = useState(false)
    const [loading, setLoading] = useState(true)
    const handleClose = () => {
      // setMetaMaskChange(!metamaskChange)
      setWrongNetwork(false)
    }
  
    const getWeb3 = () => {
      return new Promise(async (resolve, reject) => {
        if (window.ethereum) {
          const web3 = new Web3(window.ethereum)
          try {
            // await window.ethereum.send("eth_requestAccounts");
            await window.ethereum.enable()
            resolve(web3)
          } catch (e) {
            reject(e)
          }
        } else if (window.web3) {
          resolve(window.web3)
        } else {
          window.alert('Must install Metamask Extension!\nDApp will not load')
          // resolve('Must install Metamask Extension!')
        }
      })
    }
  
    useEffect(() => {
      const init = async () => {
        const web3 = await getWeb3()
        if (web3 === undefined) return
        const account = (await web3.eth.getAccounts())[0]
        const networkId = await web3.eth.net.getId()
        if (networkId !== parseInt(REACT_APP_NETWORK_ID)) {
          console.log('Not correct', networkId, REACT_APP_NETWORK_ID)
          setWrongNetwork(true)
        }
        const domain = window.location.href.split('?')[0]
        if (domain.indexOf('/busd') === -1) {
          setAsset('BNB')
        } else {
          setAsset('BUSD')
        }
        const bnbPrice = await getAssetPrice(web3)
        setAssetPrice(bnbPrice)
        setWeb3(web3)
        setAccount(account)
      }
      setLoading(true)
      init()
      setLoading(false)
    }, [])
  
  return (
    <MantineProvider theme={{ colorScheme: 'dark' }}>
       <NotificationsProvider  position="top-left" zIndex={2077}>
    <AppShell 
    styles={{
      main: {
       spacing: { xs: 15, sm: 20, md: 25, lg: 30, xl: 40 },
       // padding: '50',
        background: theme.colorScheme === 'dark' ? theme.colors.dark[8] : theme.colors.dark[8],
      },
    }}
   
    fixed
    navbar={
       <SideNavBar/>
    }
   
    // footer={
    //   <Footer height={60} p="md">
    //     Application footer
    //   </Footer>
    // }
    header={
          <HeaderNavBar account={account} asset={asset} assetPrice={assetPrice} links={[]}/>
    }
  >
 
    <Outlet context={[web3,account, asset, assetPrice]}/>

   


  </AppShell>
  </NotificationsProvider>
  </MantineProvider>

  )
}

export default DashboardLayout;
