import { createStyles, Group, Paper, SimpleGrid, Text } from '@mantine/core';
import {
  UserPlus,
  Discount2,
  Receipt2,
  Coin,
  ArrowUpRight,
  ArrowDownRight,
} from 'tabler-icons-react';
const useStyles = createStyles((theme) => ({
    root: {
     // padding: theme.spacing.xl * 1.5,
    },
  
    value: {
      fontSize: 24,
      fontWeight: 700,
      lineHeight: 1,
    },
  
    diff: {
      lineHeight: 1,
      display: 'flex',
      alignItems: 'center',
    },
  
    icon: {
      color: theme.colorScheme === 'dark' ? theme.colors.dark[3] : theme.colors.gray[4],
    },
  
    title: {
      fontWeight: 700,
      textTransform: 'uppercase',
    },
  }));
  
  const icons = {
    user: UserPlus,
    discount: Discount2,
    receipt: Receipt2,
    coin: Coin,
  };
const StatsCard = (props) => {
    const classes = useStyles();
    const Icon = icons[props.icon];
    const DiffIcon = props.diff > 0 ? ArrowUpRight : ArrowDownRight;

    return (
      <Paper withBorder p="md" radius="md" key={props.title}>
      <Group position="apart">
        <Text size="xs" color="dimmed" className={classes.title}>
          {props.title}
        </Text>
        <Icon className={classes.icon} size={22} />
      </Group>

      <Group align="flex-end" spacing="xs" mt={2}>
        <Text className={classes.value}>{props.total}</Text>
        <Text
          color={props.diff > 0 ? 'teal' : 'red'}
          size="sm"
          weight={500}
          className={classes.diff}
        >
          <span>{props.diff}%</span>
          <DiffIcon size={16} />
        </Text>
      </Group>

      <Text size="xs" color="dimmed" mt={7}>
        Compared to previous month
      </Text>
    </Paper>
    );
  }

 export default StatsCard;