import React, { useState } from 'react';
import { Card, Image, Text, Group, Badge, createStyles, Center, Button } from '@mantine/core';
import { GasStation, Gauge, ManualGearbox, Users } from 'tabler-icons-react';
import { withdraw } from '../../../utils/utils';

const useStyles = createStyles((theme) => ({
  card: {
    backgroundColor: theme.colorScheme === 'dark' ? theme.colors.dark[7] : theme.white,
  },

  imageSection: {
    padding: theme.spacing.md,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    borderBottom: `1px solid ${
      theme.colorScheme === 'dark' ? theme.colors.dark[4] : theme.colors.gray[3]
    }`,
  },

  label: {
    marginBottom: theme.spacing.xs,
    lineHeight: 1,
    fontWeight: 700,
    fontSize: theme.fontSizes.xs,
    letterSpacing: -0.25,
    textTransform: 'uppercase',
  },

  section: {
    padding: theme.spacing.md,
    borderTop: `1px solid ${
      theme.colorScheme === 'dark' ? theme.colors.dark[4] : theme.colors.gray[3]
    }`,
  },

  icon: {
    marginRight: 5,
    color: theme.colorScheme === 'dark' ? theme.colors.dark[2] : theme.colors.gray[5],
  },
}));

const mockdata = [
  { label: '4 passengers', icon: Users },
  { label: '100 km/h in 4 seconds', icon: Gauge },
  { label: 'Automatic gearbox', icon: ManualGearbox },
  { label: 'Electric', icon: GasStation },
];

const WalletCard = (props) => {
  const { classes } = useStyles();
  const [loading, setLoading] = useState(false)
	const [spinnerLoading, setSpinnerLoading] = useState(false)
	
  const userWithdraw = async () => {
		try {
			setLoading(true)
			setSpinnerLoading(true)
			if (props.web3 === undefined) return
			await withdraw(props.web3, (await props.web3.eth.getAccounts())[0])
			setLoading(false)
			setSpinnerLoading(false)
			window.location.reload()
		} catch (e) {
			console.error(e)
			alert(`Some error\n${e.message}`)
			setLoading(false)
			setSpinnerLoading(false)
		}
	}


  return (
    <Card withBorder radius="md" className={classes.card}>
      <Card.Section className={classes.imageSection}>
     
     </Card.Section>

      <Group position="apart" mt="md">
        <div>
          <Text weight={500}>Total Staked</Text>
        </div>
        <Badge size="xl" color="green"variant="outline">{props.totalDeposited}</Badge>
      </Group>

      <Card.Section className={classes.section} mt="md">
      <Group position="apart" mt="md">
        <div>
          <Text weight={500}>Available For Withdraw</Text>
    
        </div>
        <Badge size="xl" color="green"variant="outline" weight={700}>{props.availableTotal}</Badge>
      </Group>
      </Card.Section>
      

      <Card.Section className={classes.section}>
        <Group spacing={30} position="apart">
          <div>
            <Text size="xl" weight={700} sx={{ lineHeight: 1 }}>
             
            </Text>
            <Text size="sm" color="dimmed" weight={500} sx={{ lineHeight: 1 }} mt={3}>
            
            </Text>
          </div>

          <Button onClick={userWithdraw} radius="xl" size="md">
            Withdraw
          </Button>
        </Group>
      </Card.Section>
    </Card>
  );
}
export default WalletCard;


