import React, { useState, useEffect } from 'react'
import { totalStaked, totalBalance, findUserDownline, getUserTotalDeposits, getUserReferralAmount, getUserAvailable } from '../../utils/utils'

import { Container, createStyles, Grid, Group, Paper, SimpleGrid, Text } from '@mantine/core';
import {
  UserPlus,
  Discount2,
  Receipt2,
  Coin,
  ArrowUpRight,
  ArrowDownRight,
} from 'tabler-icons-react';
import StatsCard from './card/StatsCard';
import WalletCard from './card/WalletCard';
import WalletReferralCard from './card/WalletReferralCard';

const useStyles = createStyles((theme) => ({
  root: {
   // padding: theme.spacing.xl * 1.5,
  },

  value: {
    fontSize: 24,
    fontWeight: 700,
    lineHeight: 1,
  },

  diff: {
    lineHeight: 1,
    display: 'flex',
    alignItems: 'center',
  },

  icon: {
    color: theme.colorScheme === 'dark' ? theme.colors.dark[3] : theme.colors.gray[4],
  },

  title: {
    fontWeight: 700,
    textTransform: 'uppercase',
  },
}));

const icons = {
  user: UserPlus,
  discount: Discount2,
  receipt: Receipt2,
  coin: Coin,
};



export function WalletCards(props) {

	const [loading, setLoading] = useState(false)
	const [spinnerLoading, setSpinnerLoading] = useState(false)
	const [userDownline, setUserDownline] = useState(0)
	const [userReferralTotalBonus, setUserReferralTotalBonus] = useState(0)
	const [userReferralWithdrawn, setUserReferralWithdrawn] = useState(0)
	const [userTotalDeposits, setUserTotalDeposits] = useState(0)
	const [userAvailable, setUserAvailable] = useState(0)
	const [referralLink, setReferralLink] = useState('')

	useEffect(() => {
		const init = async () => {
			try {
				setLoading(true)
				if (props.web3 === undefined) return
				const account = (await props.web3.eth.getAccounts())[0]
				// const account = '0x494CEAF0059560852Aaf44310988C2d2a700C5a6'
				const userDownline = await findUserDownline(props.web3, account)
				const { totalBonus, bonusWithdrawn } = await getUserReferralAmount(props.web3, account)
				const totalDeposit = await getUserTotalDeposits(props.web3, account)
				const userAvailable = await getUserAvailable(props.web3, account)
				const domain = window.location.href.split('?')[0]
				setUserDownline(userDownline)
				setUserReferralTotalBonus(totalBonus)
				setUserReferralWithdrawn(bonusWithdrawn)
				setUserTotalDeposits(totalDeposit)
				setUserAvailable(userAvailable)
				setReferralLink(domain + '?ref=' + account)
				setLoading(false)
			} catch (e) {
				console.error(`Error at Referr ${e.message}`)
			}
		}
		init()
	}, [props])


    const { classes } = useStyles();
    const Icon = icons[props.icon];
    const DiffIcon = props.diff > 0 ? ArrowUpRight : ArrowDownRight;
  return (
    <>
    <Grid.Col span={12}>
      <Container sx={
        {
          width: '95%',
          padding: '10px',
         // display: 'flex',
        
          alignItems: 'center',
          
        }
      }>
       <WalletCard
       icon={props.icon}
        title={props.title}
        web3={props.web3}
         totalDeposited={userTotalDeposits}
          available="Available for Withdraw"
            availableTotal={userAvailable}
          />
        
          </Container>
   </Grid.Col>
   <Grid.Col span={12}>
   <Container sx={
        {
          width: '95%',
          padding: '10px',
         // display: 'flex',
        
          alignItems: 'center',
          
        }
      }>
       <WalletReferralCard 
       referralLink={referralLink} 
       icon={props.icon}
       userReferralTotalBonus={userReferralTotalBonus}
       userReferralWithdrawn={userReferralWithdrawn}
       invitedTotal={userDownline} 
         diff={-1}/>
  </Container>
   </Grid.Col>
   </>
  );
}