import React, { useState, useEffect } from 'react'
import moment from 'moment'
import { createStyles,SimpleGrid, Table, Progress, Anchor, Text, Group, ScrollArea, Grid, Container } from '@mantine/core';
import { getUserAmountOfDeposits, getUserDepositInfo } from '../../utils/utils'
import { useOutletContext } from 'react-router-dom';
import { InvestmentCard } from '../../components/dashboard/card/InvestmentCard';
import { StakeCard } from '../../components/dashboard/card/StakeCard';

const useStyles = createStyles((theme) => ({
  progressBar: {
    '&:not(:first-of-type)': {
      borderLeft: `3px solid ${theme.colorScheme === 'dark' ? theme.colors.dark[7] : theme.white}`,
    },
  },
}));


const  MyInvestment =()=> {
  const [web3, account, asset, assetPrice ] = useOutletContext();
  const [loading, setLoading] = useState(false)
	const [depositDetails, setDepositDetails] = useState([])

	useEffect(() => {
		const init = async () => {
			try {
				setLoading(true)
				if (web3 === undefined) return
				let depositAmounts = await getUserAmountOfDeposits(web3)
				const depositDetailsArray = []
				for (let i = 0; i < depositAmounts; i++) {
					const depositDetails = await getUserDepositInfo(web3, i)
					depositDetails['index'] = i
					depositDetailsArray.push(depositDetails)
				}

				setDepositDetails(depositDetailsArray)
				setLoading(false)
			} catch (e) {
				console.error(`Error at Cards ${e.message}`)
			}
		}
		init()
	}, [web3])


  const { classes, theme } = useStyles();
 
  console.log(account)
  console.log(depositDetails)
  const data = [
    {
      title: 'Business Development',
      author: asset,
      year: '2020',
      reviews: { positive: 100, negative: 0 },
    }
  ];

  const rows = depositDetails.map((e, index) => {
    console.log(account)
  
    return (
    <>
     <Grid.Col span={4}>
    <InvestmentCard web3={web3} index={e.index} key={index} plan={e.plan} percent={e.percent} amount={e.amount} start={e.start} finish={e.finish} profit={e.profit}/>
  </Grid.Col>
    </>
    );
   
  });

  return (
    <Container size="md">
    <SimpleGrid cols={1} spacing="md" breakpoints={[{ maxWidth: 'lg', cols: 1 }]}>
      <Grid grow  gutter="xl">
        { rows }
      </Grid>
      {/* <Grid grow  gutter="xl">
        <StakeCard/>
      </Grid> */}
      </SimpleGrid>
      
    </Container>
  );
}


export default MyInvestment;