import React, { useState, useEffect } from 'react'
import { totalStaked, totalBalance } from '../../utils/utils'

import { createStyles, Grid, Group, Paper, SimpleGrid, Text } from '@mantine/core';
import {
  UserPlus,
  Discount2,
  Receipt2,
  Coin,
  ArrowUpRight,
  ArrowDownRight,
} from 'tabler-icons-react';
import StatsCard from './card/StatsCard';

const useStyles = createStyles((theme) => ({
  root: {
   // padding: theme.spacing.xl * 1.5,
  },

  value: {
    fontSize: 24,
    fontWeight: 700,
    lineHeight: 1,
  },

  diff: {
    lineHeight: 1,
    display: 'flex',
    alignItems: 'center',
  },

  icon: {
    color: theme.colorScheme === 'dark' ? theme.colors.dark[3] : theme.colors.gray[4],
  },

  title: {
    fontWeight: 700,
    textTransform: 'uppercase',
  },
}));

const icons = {
  user: UserPlus,
  discount: Discount2,
  receipt: Receipt2,
  coin: Coin,
};



export function StatsStakedCard(props) {

	const [loading, setLoading] = useState(false)
	const [totalStake, setTotalStake] = useState(0)
	const [totalBalanceOf, setTotalBalanceOf] = useState(0)

	useEffect(() => {
		const init = async () => {
			try {
				setLoading(true)
				if (props.web3 === undefined) return
				const _totalStake = await totalStaked(props.web3)
				const _totalBalance = await totalBalance(props.web3)
				setTotalStake(_totalStake)
				setTotalBalanceOf(_totalBalance)
				setLoading(false)
			} catch (e) {
				console.error(`Error at Hero ${e.message}`)
			}
		}
		init()
	}, [props])
  

    const { classes } = useStyles();
    const Icon = icons[props.icon];
    const DiffIcon = props.diff > 0 ? ArrowUpRight : ArrowDownRight;
  return (
    <>
    <Grid.Col span={6}>
       <StatsCard icon={props.icon} assetPrice={props.assetPrice} title={props.title} total={totalStake.toFixed(3)+'~($'+totalStake.toFixed(3)*280+')'} diff={1}/>
   </Grid.Col>
   <Grid.Col span={6}>
       <StatsCard icon={props.icon} assetPrice={props.assetPrice} title={props.balanceTitle} total={totalBalanceOf.toFixed(3)+'~($'+totalBalanceOf.toFixed(3)*280+')'} diff={-1}/>
   </Grid.Col>
   </>
  );
}